@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500&display=swap);
* {
  box-sizing: border-box;
}
body {
  background:url("http://www.sucessfulstartups.com/wp-content/uploads/2020/03/book-images.jpg") !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}
.Form{
  display: block;
  justify-content: center;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.Shelf ul{
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
}
.Shelf li{
    display: inline-block;
    margin: 20px;
}

.Shelf a{
    text-decoration: none;
    color:  rgb(37, 228, 196);
}
.Shelf a:hover,
.Shelf a:active{
    color: orange;
}

.Stylelogo{
    padding-top: 45px;
    width:100%;
    justify-content: center;
    display: flex;
    height: 150px;
    box-shadow: 100px 18px 6px -6px rgb(20, 174, 235);
    border-radius: 70%;
    border-bottom:  rgb(9, 217, 224);
}
.Stylelogo :hover{
    transform: scale(1.5);
}


.form{
  opacity: 0.5;
  display: flex;
  padding-top:65px;
  justify-content: center;
  background-color: rgb(248, 248, 248);

}
.form-group{
  display: flex;
  padding: 25px 0 25px 0;
  margin: 10px;
  justify-content: center;
  margin: 15px;
  opacity: 0.9 !important;

  border-radius: 50%;
}
h3{
  justify-content: center;
  display: flex;
  -webkit-text-decoration: darkorange;
          text-decoration: darkorange;
  color: goldenrod;

}
.draw-border {
  box-shadow: inset 0 0 0 4px #58cdd1;
  color: #58afd1;
  transition: color 0.25s 0.0833333333s;
  position: relative;
}

.draw-border::before,
.draw-border::after {
  border: 0 solid transparent;
  box-sizing: border-box;
  content: '';
  pointer-events: none;
  position: absolute;
  width: 0rem;
  height: 0;
  bottom: 0;
  right: 0;
}

.draw-border::before {
  border-bottom-width: 4px;
  border-left-width: 4px;
}

.draw-border::after {
  border-top-width: 4px;
  border-right-width: 4px;
}

.draw-border:hover {
  color: #ffe593;
}

.draw-border:hover::before,
.draw-border:hover::after {
  border-color: #eb196e;
  transition: border-color 0s, width 0.25s, height 0.25s;
  width: 100%;
  height: 100%;
}

.draw-border:hover::before {
  transition-delay: 0s, 0s, 0.25s;
}

.draw-border:hover::after {
  transition-delay: 0s, 0.25s, 0s;
}

.btn {
  background: none;
  border: none;
  cursor: pointer;
  line-height: 1.5;
  font: 700 1.2rem 'Roboto Slab', sans-serif;
  padding: 0.75em 2em;
  letter-spacing: 0.05rem;
  margin: 1em;
  width: 13rem;
}

.btn:focus {
  outline: 2px dotted #55d7dc;
}



.GIF{
    background-color: rgb(255, 255, 255);
    padding:8px;
    height: 100%;
    box-sizing: border-box;
    border-radius: 100%;
    margin: 16px auto;
    border: 1rem solid slateblue;
    display: flex;
    justify-content: center;

}
.Logo{
    background-color: rgb(255, 255, 255);
    padding:8px;
    height: 100%;
    box-sizing: border-box;
    border-radius: 100%;
}
.Logo img{
    height: 100%;
    animation: rotation 8s infinite linear;

}
.Logo :hover{
  background-color: rgba(155, 78, 243, 0.774);
  box-sizing: border-box;
  border-radius: 100%;
  
    
}
@keyframes rotation{from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(359deg);
  }
}

.scroll{
    margin: auto;
    text-align: center;
    font-weight: bold;
    height: 250px;
    font-size: 1.2rem;
}
.container {
  display: inline-flex;
  grid-template-rows: 300px 300px 300px;
  grid-gap: 50px;
  /* width: 100%; */
  justify-content: center ;
  align-items: center;
  height: auto;
  padding:5px;
  background-color: #f5f5f5;
  font-family: 'Baloo Paaji 2', cursive;
    
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
  
}

